@import "https://fonts.googleapis.com/css2?family=Cormorant:ital,wght@0,400;0,600;1,400;1,600&family=Mulish:wght@400;600;800&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.falling {
  animation-name: falling;
}

.falling2 {
  animation-name: falling2;
}

.fallingRight {
  animation-name: fallingRight;
}

.photo-animation {
  width: 100%;
  height: 1px;
  position: absolute;
  top: 430px;
  right: 135px;
}

@media (min-width: 768px) {
  .photo-animation {
    top: 620px;
    right: 250px;
  }
}

@media (min-width: 1200px) {
  .photo-animation {
    display: block;
    top: -120px;
    right: 285px;
  }
}

.photocard {
  pointer-events: none;
  position: absolute;
  top: 0;
  right: 0;
  transform: translateY(0);
}

@keyframes falling {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
    transform: translateY(0)rotate(0deg);
  }

  90% {
    opacity: .8;
    transform: translateY(480px)rotate(200deg)scale(3);
  }

  100% {
    opacity: .2;
    transform: translateY(480px)rotate(210deg)scale(3);
  }
}

@keyframes falling2 {
  0% {
    opacity: 0;
    transform: rotate(20deg);
  }

  10% {
    opacity: 1;
    transform: translateY(0);
  }

  90% {
    opacity: .8;
    transform: translateY(480px)rotate(280deg)scale(3);
  }

  100% {
    opacity: .2;
    transform: translateY(480px)rotate(290deg)scale(3);
  }
}

@keyframes fallingRight {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
    transform: translateY(0)rotate(20deg);
  }

  90% {
    opacity: .8;
    transform: translateY(480px)translateX(50px)rotate(-260deg);
  }

  100% {
    opacity: .2;
    transform: translateY(480px)translateX(50px)rotate(-270deg);
  }
}

@keyframes fallingLeft {
  0% {
    opacity: 0;
  }

  10% {
    opacity: 1;
    transform: translateY(0)rotate(20deg);
  }

  90% {
    opacity: .8;
    transform: translateY(480px)translateX(-75px)rotate(240deg);
  }

  100% {
    opacity: .2;
    transform: translateY(480px)translateX(-75px)rotate(220deg);
  }
}

:root {
