  --swiper-navigation-sides-offset: 20px;
  --swiper-theme-color: #235f83 !important;
  --swiper-pagination-top: 0 !important;
  --swiper-pagination-bullet-size: fit-content !important;
  --swiper-pagination-bullet-border-radius: 0 !important;
  --swiper-pagination-color: $ accent-color !important;
  --swiper-pagination-bullet-inactive-color: $ accent-color !important;
  --swiper-pagination-bullet-inactive-opacity: .5 !important;
  --swiper-pagination-bullet-opacity: 1 !important;
}

